.data-lengkap-container {
  padding: 20px;
  font-family: "Inter", sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh;
}

.logo7{
  width: 130px;
  height: auto;
}

.geo-header1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
  left: 1px;
}

.geo-back1-button{
  background: none;
  border: none;
  cursor: pointer !important;
}

.geo-back1-button img {
  width: 24px;
  height: 24px;
}

.section-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.circle-button {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
  background-color: #0ca5eb;
  color: white;
  font-weight: bold;
}

.circle-button.active {
  background-color: red;
}

.section-title {
  text-align: center;
  margin-bottom: 20px;
}

.section-subtitle {
  font-size: 14px;
  color: white;
  background-color: #5dbf50;
  padding: 5px 5px;
  border-radius: 10px;
}

.form-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-box {
  background: white;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  box-sizing: border-box;
}

.input-label {
  font-weight: 470;
  margin-bottom: 1px;
  display: block;
}

.required-text {
  color: #000;
  background-color: #ffb7a7;
  padding: 4px 8px;
  border-radius: 10px;
  font-size: 11px;
  display: inline-block;
  margin-bottom: 4px;
  margin-top: 1px;
  font-weight: 470;
}

.form-input {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
  box-sizing: border-box;
  font-weight: 470;
}

.form-footer {
  display: flex;
  justify-content: flex-start;
  margin-top: 20px;
}

.next-button {
  background-color: #008cff;
  color: white;
  font-size: 12px;
  padding: 6px 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.next-button:hover {
  background-color: #0070cc;
}


