* {
  font-family: 'inter', sans-serif;
}

.scroll-wrapper {
  max-height: 100vh;
  overflow-y: auto;
  padding-bottom: 20px;
  box-sizing: border-box;
}

.register-container {
  padding: 20px;
  background-color: #fff;
}

.register-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
}

h3 {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.3px;
  color: #747373;
  font-family: 'IBM Plex Sans', sans-serif;
  text-align: center;
}

.register-header img {
  height: 24px;
  width: 24px;
}

.logo-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.logo-container h2 {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: -0.3px;
}

.logo {
  padding-right: 10px;
}

.back-button2 {
  position: absolute;
  background: none;
  border: none;
  cursor: pointer;
  left: 1px;
  padding: 0;
}

.back-button2 img {
  width: 24px;
  height: 24px;
}

#HKN {
  width: 130px;
  height: auto;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-top: 10px;
}

.register-input {
  padding-left: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
  height: 38px;
  font-weight: 400;
  letter-spacing: 0.14px;
  color: #292B32;
}

.register-button {
  font-weight: 600;
  padding: 12px;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  margin-top: 10px;
  cursor: pointer !important;
}

.register-button:disabled {
  cursor: not-allowed;
  background-color: #050C2E1A;
  color: #050C2E3D;
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.input-wrapper .register-input {
  width: 100%;
  padding-right: 40px; /* ruang untuk ikon */
}

.eye-icon3 {
  position: absolute;
  right: 10px;
  color: #999;
  cursor: pointer;
  font-size: 18px;
}


.error-message {
  font-size: 12px;
  color: red;
  margin-top: 2px;
}

.success-message {
  color: green;
  font-size: 13px;
  margin-top: 8px;
}