.splash-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  width: 100%;
  padding: 40px 20px;
  box-sizing: border-box;
  background-color: #0E0F11;
}

.logo-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 267px;
  height: 267px;
}

.logo-glow {
  position: absolute;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, #FFB532 0%, transparent 70%);
  filter: blur(60px); /* Jangan terlalu besar */
  border-radius: 50%;
  opacity: 1;
  z-index: 0;
}

.splash-logo {
  width: 150px;
  height: auto;
  z-index: 1;
}

.splash-footer {
  font-size: 13px;
  color: #ccc;
  text-align: center;
  margin-bottom: 30px;
  font-family: 'IBM Plex Sans', sans-serif;
}
