.dl4-container {
  padding: 20px;
  font-family: "Inter", sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh;
}

.dl4-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
}

.dl4-back-button {
  background: none;
  border: none;
  cursor: pointer;
}

.dl4-back-button img {
  width: 24px;
  height: 24px;
}

.dl4-section-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.dl4-circle-button {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
  background-color: #0ca5eb;
  color: white;
  font-weight: bold;
}

.dl4-circle-button.active {
  background-color: red;
}

.dl4-section-title {
  text-align: center;
  margin-bottom: 20px;
}

.dl4-section-subtitle {
  font-size: 14px;
  color: green;
}

.dl4-form-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.dl4-input-box {
  background: white;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  box-sizing: border-box;
}

.dl4-input-label {
  font-weight: 470;
  margin-bottom: 1px;
  display: block;
}

.dl4-required-text {
  color: #a94442;
  background-color: #f2dede;
  padding: 4px 8px;
  border-radius: 5px;
  font-size: 13px;
  display: inline-block;
  margin-bottom: 4px;
  margin-top: 1px;
  font-weight: 470;
}

.dl4-form-input {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
  font-weight: 470;
  box-sizing: border-box;
}

.dl4-form-footer {
  display: flex;
  justify-content: flex-start;
  margin-top: 20px;
}

.dl4-next-button {
  background-color: #008cff;
  color: white;
  font-size: 12px;
  padding: 6px 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.dl4-next-button:hover {
  background-color: #0070cc;
}
