*{
  font-family: 'inter', sans-serif;
}

.scroll-wrapper {
  max-height: 100vh;
  overflow-y: auto;
  padding-bottom: 20px;
  box-sizing: border-box;
}

.register-container {
  padding: 20px;
  font-family: sans-serif;
  background-color: #fff;
}

.register-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: center;
  gap: 0;
  margin: 0;
}
 h3 {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.3px;
    color: #747373;
    font-family: 'IBM Plex Sans', sans-serif;
    vertical-align: middle;
 }
.register-header img{
    height: 24px;
    width: 24px;
}

.logo-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.logo-container h2{
  font-size: 20px;
  font-weight: 700;
  letter-spacing: -0.3px;
}

.logo {
  padding-right: 10px;
}


.back-button1 {
  position: absolute;
  background: none;
  border: none;
  cursor: pointer;
  left: 1px;
  padding: 0;
}

#HKN{
  width: 130px;
  height: auto;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-top: 10px;
}

.register-form input,
.register-form textarea,
.register-form select {
  padding-left: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-family: 'inter', sans-serif;
  font-weight: 400;
  letter-spacing: 0.14px;
  line-height: 100%;
  color: #292B32;
}

.register-form textarea {
  resize: vertical;
  min-height: 60px;
}

.register-button {
  font-weight: 600;
  padding: 12px;
  border: none;
  letter-spacing: -0.16px;
  border-radius: 20px;
  font-size: 16px;
  margin-top: 10px;
}

.register-button:disabled {
  cursor: not-allowed !important;
  background-color: #050C2E1A !important;
  color: #050C2E3D !important;
}

.register-button :enabled{
  cursor: pointer !important;
}

.register-form label {
  display: block;
  margin-bottom: 0;
  margin-top: 12px;
  font-size: 14px;
  font-weight: 600;
  color: #292B32;
}
.register-form ::placeholder{
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #0E0F1173;
  letter-spacing: 0.14px;
  line-height: 100%;
  padding-left: 8px;
}

.register-input input{
  font-family: 'inter', sans-serif;
  font-weight: 600;
  color: #ccc;
}

.register-input{
  cursor: pointer !important;
  height: 38px;
}

.password-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-wrapper input {
  width: 100%;
  padding-right: 36px; /* ruang untuk icon */
}

.eye-icon1 {
  position: absolute;
  right: 10px !important;
  bottom: 10px !important;
  cursor: pointer !important;
  color: #888;
}

.Hardcore{
  background-color: #d3d3d3;
}

.popup-error {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #f44336;
  color: white;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  z-index: 9999;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: fadeInOut 3s ease forwards;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
  10% {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
}

