/* Mencegah scroll vertikal yang tidak perlu */
html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  max-width: 100vw;
  overscroll-behavior-y: contain;
}

.home-container {
  font-family: sans-serif;
  padding: 1rem;
  padding-bottom: 80px;
}

.home-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  background-color: #f4f4f4;
  padding: 1rem;
  margin: 0 -1rem; /* hilangkan padding container */
}

.spacer {
  flex-grow: 1;
}

.header-user-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.logo {
  max-width: 50px;
  height: auto;
}

.location-text {
  font-size: 0.85rem;
  color: gray;
}

.hi-text {
  font-weight: bold;
}

.user-photo {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-left: auto;
  object-fit: cover;
}

.section h3 {
  margin: 1rem 0 0.3rem;
}

.hero-box {
  position: relative;
  margin-top: 0.5rem;
}

.hero-img {
  width: 100%;
  height: 420px;
  object-fit: cover;
}

.hero-caption {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: 20px 16px 30px; /* atas, kanan/kiri, bawah */
  box-sizing: border-box;
  text-align: center;
  z-index: 2;
  color: white;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9), transparent);
}

.hero-caption p {
  color: #ddd; /* soften subtitle */
}

.hero-carousel {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  gap: 1rem;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  margin-bottom: 1rem;
}

.hero-slide {
  min-width: 100%;
  scroll-snap-align: start;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
}

.cta-button {
  background-color: #facc15;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  margin: 0 auto;
  display: block;
}

.event-list {
  list-style: none;
  padding: 0;
}

.event-card {
  display: flex;
  gap: 1rem;
  background: #fafafa;
  padding: 0.8rem;
  margin-top: 0.6rem;
  border-radius: 10px;
  align-items: center;
}

.event-card img {
  width: 60px;
  height: 60px;
  border-radius: 8px;
  object-fit: cover;
}

.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  background: white;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav-item {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: gray;
}

.nav-item.active {
  color: #facc15;
}
