* {
  font-family: 'inter', sans-serif;
}

.scroll-wrapper {
  max-height: 100vh;
  overflow-y: auto;
  padding-bottom: 20px;
  box-sizing: border-box;
}

.register-container {
  padding: 20px;
  background-color: #fff;
}

.register-header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  position: relative;
}

.register-header img {
  height: 24px;
  width: 24px;
}

.logo-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
}

.logo-container h2 {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: -0.3px;
}

h3 {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.3px;
  color: #747373;
  font-family: 'IBM Plex Sans', sans-serif;
}

.back-button4 {
  position: absolute;
  left: 1px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

#HKN {
  width: 130px;
  height: auto;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-top: 10px;
}

.register-form input,
.register-form select {
  padding-left: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-family: 'inter', sans-serif;
  font-weight: 400;
  color: #292B32;
  height: 38px;
}

.register-form label {
  margin-top: 12px;
  font-size: 14px;
  font-weight: 600;
  color: #292B32;
}

.register-form ::placeholder {
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #0E0F1173;
  letter-spacing: 0.14px;
  padding-left: 8px;
}

.register-button {
  font-weight: 600;
  padding: 12px;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  margin-top: 10px;
  letter-spacing: -0.16px;
}

.register-button:disabled {
  cursor: not-allowed;
  background-color: #050C2E1A;
  color: #050C2E3D;
}

.popup-error {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #f44336;
  color: white;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  z-index: 9999;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: fadeInOut 3s ease forwards;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
  10% {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
}
