/* Kontainer utama */
.container {
  padding-bottom: 80px; /* ruang bawah untuk nav */
  font-family: Arial, sans-serif;
}

/* Header */
.header {
  text-align: center;
  padding: 16px;
  font-weight: bold;
  font-size: 10px;
}

/* Flex header with centered title and back button */
.header-flex {
  position: relative;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 16px;
  border-bottom: 1px solid #eee;
}

/* Tombol kembali */
.back-btn {
  position: absolute;
  left: 16px;
  background: none;
  border: none;
  font-size: 22px;
  cursor: pointer;
  color: #007bff;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

/* Judul tetap di tengah */
.title {
  font-size: 18px;
  font-weight: bold;
  margin: 0;
  text-align: center;
}

/* Header with back button */
.header-row {
  display: flex;
  align-items: center;
  padding: 16px;
  font-weight: bold;
  font-size: 16px;
}

.back-btn {
  background: none;
  border: none;
  font-size: 40px;
  margin-right: 12px;
  cursor: pointer;
  color: #000000;
  font-weight: bold;
}

/* Fade animation */
.fade-transition {
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.3s ease;
}

.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.fade-out {
  opacity: 1;
}

.title {
  margin: 0;
}

/* Pencarian */
.search-wrapper {
  padding: 0 16px 10px;
}

.search-input {
  display: flex;
  align-items: center;
  background-color: #f3f3f3;
  border-radius: 12px;
  padding: 8px 12px;
  border: 1px solid #ddd;
}

.search-icon {
  margin-right: 8px;
  color: #888;
}

.input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  font-size: 16px;
}

/* Daftar alumni */
.list {
  padding: 0 16px;
}

.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #eee;
  font-size: 16px;
  color: #333;
  cursor: pointer;
}

.chevron {
  color: #999;
}

.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;   /* Pusatkan konten vertikal */
  height: 56px;          /* Tinggi tetap */
  padding: 0 8px;
  z-index: 1000;
  background: white;
  border-top: 1px solid #ccc;
}

.nav-btn {
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;   /* Pusat vertikal */
  align-items: center;
  height: 100%;              /* Penuhi tinggi nav */
  font-size: 12px;
}

.nav-btn.active {
  background-color: #000;
  color: #ffc107;
  border-radius: 10px;
  font-weight: bold;
  padding: 6px 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 6px;
  height: 70%;               /* Sedikit lebih kecil biar tetap rata */
  align-self: center;        /* Pusatkan dalam nav */
}

.nav-btn:not(.active) .nav-label {
  display: none;
}

.nav-label {
  font-size: 10px;
  margin-top: 0px;
}

/* Chatbox sebagai pop-up tengah */
.chatbox-container {
  position: fixed;
  bottom: 50%;
  right: 50%;
  transform: translate(50%, 50%);
  width: 90%;
  max-width: 360px;
  height: 420px;
  box-shadow: 0 0 20px rgba(0,0,0,0.2);
  border-radius: 16px;
  overflow: hidden;
  background: white;
  z-index: 1100;
  display: flex;
  flex-direction: column;
}

/* Header chatbox */
.chatbox-header {
  background: #007bff;
  color: white;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chatbox-header button {
  background: transparent;
  border: none;
  font-size: 20px;
  color: white;
  cursor: pointer;
}

/* Isi pesan */
.chatbox-messages {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
  background: #f9f9f9;
}

.message {
  margin-bottom: 10px;
  padding: 8px 12px;
  border-radius: 15px;
  max-width: 80%;
  word-wrap: break-word;
}

.message.user {
  background: #007bff;
  color: white;
  align-self: flex-end;
}

.message.bot {
  background: #e5e5ea;
  color: #333;
  align-self: flex-start;
}

/* Input chat */
.chatbox-input {
  padding: 10px;
  border-top: 1px solid #ddd;
  display: flex;
  gap: 8px;
}

.chatbox-input textarea {
  flex: 1;
  resize: none;
  padding: 8px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 14px;
  font-family: inherit;
}

.chatbox-input button {
  background: #007bff;
  border: none;
  color: white;
  padding: 0 16px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
}

.chatbox-input button:disabled {
  background: #a0c4ff;
  cursor: not-allowed;
}

.pagination {
  position: fixed;
  bottom: 70px; /* sesuaikan dengan tinggi navigasi bawah */
  left: 50%;
  transform: translateX(-50%);  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  margin: 20px 0;
  flex-wrap: wrap;
}

.page-btn {
  border: none;
  background: #f3f3f3;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
  font-weight: 500;
  transition: background 0.2s;
}

.page-btn.active {
  background-color: #ff9800; /* Warna oranye */
  color: white;
  font-weight: bold;
  border-radius: 20px;
}

.page-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.ellipsis {
  padding: 6px 12px;
  font-size: 14px;
  color: #999;
}