.popup-success {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #4CAF50;
  color: white;
  padding: 14px 24px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  z-index: 9999;
  box-shadow: 0px 4px 12px rgba(0,0,0,0.15);
  animation: fade-in-out 3s ease-in-out;
}

@keyframes fade-in-out {
  0% { opacity: 0; transform: translateX(-50%) translateY(-20px); }
  10% { opacity: 1; transform: translateX(-50%) translateY(0); }
  90% { opacity: 1; transform: translateX(-50%) translateY(0); }
  100% { opacity: 0; transform: translateX(-50%) translateY(-20px); }
}


.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 20px;
  background-color: #ffffff;
  max-width: 100%;
  width: 500px;
  min-height: 100%; /* penting: agar isi penuh tinggi layar */
  box-sizing: border-box;
  border-radius: 10px;
  font-family: 'inter', sans-serif;
  font-weight: 600;
}

.HKN {
  width: 130px;
  height: auto;
  padding-right: 10px;
}

.login-title {
  font-size: 20px;
  letter-spacing: -0.30px;
  font-weight: 700;
  margin-bottom: 30px;
  color: #333;
}

.login-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0, 24px, 24px, 24px;
}

.login-form label {
  align-self: flex-start;
  margin-left: 5px; /* sesuaikan agar sejajar dengan input */
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: bold;
  color: #292B32; /* opsional: warna hitam */
  padding-top: 15px;
}

.input-group {
  position: relative;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 10px;
}

.login-input {
  padding: 9px;
  border-radius: 10px;
  border: 1px solid #ccc;
  width: 95%;
  max-width: 400px;
  cursor: pointer !important;
  font-weight: 400;
  font-size: 14px;
  color: #292B32;
  margin-right: 0;
}

.login-input::placeholder{
  color: #0E0F1173;
}

.login-extra {
  text-align: right;
  margin-bottom: 20px;
  margin-top: 20px;
}

.login-forgot {
  background: none;
  border: none;
  color: #ffb532;
  font-size: 14px;
  cursor: pointer !important;
  font-family: 'inter', sans-serif;
  font-weight: 600;
}

.login-button {
  background-color: #ffb532;
  color: #292B32;
  padding: 12px;
  border: none;
  border-radius: 20px;
  margin-bottom: 10px;
  font-size: 16px;
  letter-spacing: -0.16px;
  cursor: pointer !important;
  width: 327px;
  height: 44px;
  font-family: 'inter', sans-serif;
  font-weight: 600;
  margin-top: 20px;
}

.login-footer {
  margin-top: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #555;
  letter-spacing: -0.07px;
}

.register-link {
  background: none;
  border: none;
  color: #ffb532;
  font-size: 14px;
  cursor: pointer !important;
  text-decoration: none;
  padding: 0;
  margin-left: 0;
  font-family: 'inter', sans-serif;
  font-weight: 700;
}

input{
  font-family: 'inter', sans-serif;
  font-weight: 600;
}

.error-message {
  display: block;
  text-align: left;
  color: #DB2A39;
  font-size: 12px;
  font-weight: 400;
  margin-top: 7px;
  margin-bottom: 0;
  padding-left: 5px;
  margin-left: 0;
}

.eye-icon {
  position: absolute;
  right: 10px !important;
  top: 46px !important;
  cursor: pointer !important;
  color: #999;
  font-size: 18px;
  z-index: 2;
}

.login-button.disabled {
  background-color: #050C2E1A;
  color: #999;
  cursor: not-allowed !important;
}
