.data-lengkap2-container {
  padding: 20px;
  font-family: "Inter", sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh;
}

.form-section2 {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-box2 {
  background: white;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  box-sizing: border-box;
}

.input-label2 {
  font-weight: 470;
  margin-bottom: 1px;
  display: block;
}

.required-text2 {
  color: #a94442;
  background-color: #f2dede;
  padding: 4px 8px;
  border-radius: 5px;
  font-size: 13px;
  display: inline-block;
  margin-bottom: 4px;
  margin-top: 1px;
  font-weight: 470;
}

.form-input2 {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
  font-weight: 470;
  box-sizing: border-box;
  resize: vertical;
}

textarea.form-input2 {
  min-height: 80px;
}
