.profile-wrapper {
  max-width: 100%;
  margin: 0 auto;
  padding: 16px;
  background-color: #f5f5f5;
  font-family: 'Inter', sans-serif;
  height: 100vh;
  overflow-y: auto;
  box-sizing: border-box;
}


.box {
  background-color: #ffffff;
  border-radius: 12px;
  padding: 16px;
  width: 100%; /* Lebar penuh layar */
  margin-left: calc(-1 * 16px); /* mengimbangi padding parent */
  margin-right: calc(-1 * 16px);
  margin-bottom: 16px;
  box-shadow: 0 0 8px rgba(0,0,0,0.05);
}

.box-header h2 {
  font-size: 20px;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

.box-profile {
  display: flex;
  align-items: center;
  gap: 12px;
}

.profile-img-wrapper {
  width: 70px;
  height: 70px;
  border-radius: 100px;
  overflow: hidden;
  border: 2px solid #ccc;
  flex-shrink: 0;
}

.profile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.profile-info h3 {
  font-size: 16px;
  margin: 0;
}
.profile-info p {
  font-size: 13px;
  color: #555;
  margin: 2px 0;
}

.box-idcard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.box-kartu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: linear-gradient(to right, #292B32 30%, #FFB532 100%);
  border-radius: 12px;
  max-width: 327px;
  width: 100%;
  max-height: 180px;
  height: 180px;
  box-sizing: border-box;
  color: white;
}

.idcard-logo {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.idcard-logo img {
  width: 60px;
  height: auto;
}

.idcard-text h4 {
  margin: 4px 0 0;
  font-size: 14px;
}
.idcard-text p {
  font-size: 12px;
  color: #777;
}

.idcard-photo img {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 8px;
}

.default-icon-wrapper,
.default-id-icon-wrapper {
  width: 70px;
  height: 70px;
  border-radius: 100px;
  background-color: #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
}

.default-id-icon-wrapper {
  width: 64px;
  height: 64px;
  border-radius: 8px;
}

.default-icon,
.default-id-icon {
  font-size: 28px;
  color: #555;
}

.box-akun h4,
.box-info h4 {
  font-size: 16px;
  margin-bottom: 8px;
}

.box-akun{
  text-align: left;
}

.nav-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #eee;
  font-size: 14px;
  color: #333;
  width: 100%;
  gap: 10px;
}

.nav-icon {
  flex-shrink: 0;
  font-size: 16px;
  color: #444;
}

.nav-button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 14px;
  color: #333;
  text-align: left;
  flex-grow: 1;
  cursor: pointer !important;
  font-family: inherit;
}

.nav-button:focus {
  outline: none;
}

.chevron-icon {
  font-size: 14px;
  color: #999;
}

.logout-button {
  width: 100%;
  padding: 12px;
  font-weight: 600;
  font-size: 15px;
  background-color: #FFB532;
  color: #292B32;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  background: white;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav-item {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: gray;
}

.nav-item.active {
  color: #facc15;
}
