.chat-bubble {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1rem;
  animation: fadeIn 0.3s ease-in-out;
}

.chat-bubble.user {
  flex-direction: row-reverse;
}

.chat-bubble img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin: 0 8px;
}

.bubble-text {
  background-color: #f1f1f1;
  padding: 10px 14px;
  border-radius: 16px;
  max-width: 75%;
  white-space: pre-wrap;
  line-height: 1.5;
  font-size: 0.95rem;
  color: #333;
}

.chat-bubble.user .bubble-text {
  background-color: #dbeafe;
}

.alumni-link {
  color: blue;
  cursor: pointer;
  text-decoration: underline;
}

.marker {
  display: block;
}

.alumni-card:hover {
  background-color: #f0f0f0;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to   { opacity: 1; transform: translateY(0); }
}

