html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  font-family: 'inter', sans-serif;
}

.verifikasi-container {
  padding: 20px;
  font-family: sans-serif;
  background-color:#ffffff;
  max-width: 100%;
  min-height: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.verifikasi-header {
  display: flex;
  align-items: center;
  gap: 10px;
  color: rgb(0, 0, 0);
  padding: 10px 0;
}

.back-button3 {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  left: 1px;
}

.back-button3 img {
  width: 24px;
  height: 24px;
}

.verifikasi-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  text-align: center;
  width: 100%;
  max-width: 400px  ;
  padding: 30px 20px;
  background-color: #ffffff;
  border-radius: 10px;
  margin-bottom: 50px;
}

.verifikasi-main h2 {
  margin: 0 auto;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: -0.3;
}

.verifikasi-main img {
  padding-bottom: 20px;
  width: 100px;
  height: auto;
}

.verifikasi-main p {
  margin: 0;
  font-size: 16px;
  padding-top: 8px;
  font-family: 'IBM plex Sans', sans-serif;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.3px;
}

.phone-number {
  font-family: 'IBM plex Sans', sans-serif;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.3px;
  font-size: 16px;
}

/* OTP Input Container */
.otp-box {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 15px;
}

/* Masing-masing kotak digit OTP */
.otp-digit {
  width: 40px;
  height: 50px;
  font-size: 20px;
  line-height: 30px;
  font-weight: 500;
  letter-spacing: 0.3px;
  text-align: center;
  border: 2px solid #FFB532;
  border-radius: 6px;
  outline: none;
  transition: border-color 0.2s ease;
  cursor: pointer !important;
}

.otp-digit:focus {
  border-color: #FFB532;
}

.error-text {
  color: red;
  font-size: 12px;
  margin-top: 5px;
}

.verifikasi-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-top: 20px;
}

.verifikasi-button {
  background-color: #050C2E1A;
  color: #050C2E3D;
  padding: 12px 40px;
  border: none;
  border-radius: 15px;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: -0.16px;
  line-height: 100%;
  cursor: not-allowed;
  transition: background-color 0.3s ease;
  max-width: 100%;
  width: 327px;
  height: 44px;
}

.verifikasi-button.active {
  background-color: #FFB532;
  color: #292B32;
  cursor: pointer !important;
}

.resend-text {
  font-size: 14px;
  color: #555;
  cursor: pointer;
  text-decoration: none;
  border: none;
  background: none;
  margin-top: 20px;
}

.resend-button {
  background: none;
  border: none;
  color: #ffb532; 
  font-weight: 500;
  margin-left: 5px;
  cursor: pointer !important;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.3px;
  padding: 0;
}

.skip-button {
  margin-top: 1rem;
  background: none;
  border: none;
  color: #007bff;
  font-weight: bold;
  cursor: pointer;
  text-decoration: underline;
}

.skip-button:hover {
  color: #0056b3;
}

